<template>
  <div>
    <v-list class="mx-10">
      <v-list-item v-for="message in messages" :key="message">
        <v-list-item-title
          >{{ message.content }} | {{ message.id }}</v-list-item-title
        >
      </v-list-item>
    </v-list>

    <app-input-message
      class="mx-10 my-4"
      @send-message="addMessage"
    ></app-input-message>
  </div>
</template>

<script>
import InputMessage from './InputMessage'
import uuid from 'uuid/dist/v4'

export default {
  data: () => ({
    messages: [{ id: 'asdad', content: 'I want to die:(' }]
  }),
  components: {
    'app-input-message': InputMessage
  },
  methods: {
    addMessage(message) {
      this.messages.push({ id: uuid(), content: message })
    }
  }
}
</script>
